.react-datepicker__input-container input {
    padding: 0.54rem !important;
}

.view-list-image {
    width: 100px;
    height: 70px;
    border-radius: 5px;
    margin-right: 10px;
}

.rc-steps-item-icon {
    background-color: #54c2a2;
}

.rc-steps-item-process .rc-steps-item-icon {
    background: #54c2a2;
}

.rc-steps-item-finish .rc-steps-item-icon {
    border-color: #54c2a2;
    background-color: #54c2a2;
}

.finish-icon {
    position: relative;
    display: flex;
}

.finish-icon::after {
    content: " ";
    display: block;
    width: 7px;
    height: 12px;
    border: solid #fff;
    border-width: 0 2px 2px 0;
    position: absolute;
    left: 9px;
    top: 5px;
    transform: rotate(45deg);
}

.rc-steps-item-process .rc-steps-icon,
.rc-steps-item-wait .rc-steps-icon {
    display: none;
}

.rc-steps-item-process .rc-steps-item-icon {
    background: transparent;
    border-color: #54c2a2;
}

.rc-steps-item-finish .rc-steps-item-tail:after{
    background-color: #54c2a2 !important;
}
